.default-links-nodeco a {
  color: #07c;
  text-decoration: none;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
.default-links-nodeco a:hover, .default-links-nodeco a:focus {
    text-decoration: underline;
}
.default-links-nodeco a:visited {
    color: purple;
}
