  max-width: 20em;
  width: 15em;
  -webkit-transition: all 0.1s ease-in;
  transition: all 0.1s ease-in;
}
.border-btn:hover {
  cursor: pointer;
}
.border-light-grey {
  border-color: #f2f6f7;
}
.bg-blue {
  -webkit-filter: blur(25px);
          filter: blur(25px);
